.sec_pankuzu {
    position: relative;
    background: $bkLightbeige;
    z-index: 1;
    // box-shadow: 0px 0px 10px rgb(0 0 0 / 10%);
    // @include pc {
    //     box-shadow: 0px 1px 10px -5px rgba(0, 0, 0, 0.5);
    // }
}
.breadcrumb {
    padding: 12.5px 0 12.5px 0;
    font-family: $baseFont;
    font-size: 1rem;
    color: $darkTxtCol;
    @include pc {
        padding: 10px 0 10px 0;
    }
}

.breadcrumb_link {
    color: $darkTxtCol;
    margin-right: 0.5em;
    width: 100%;
    line-height: 1.7;

    @include pc {
        &:hover {
            text-decoration: underline;
        }
    }
    &--active {
        color: $gold;
        text-decoration: initial;
        @include pc {
            &:hover {
                text-decoration: none;
            }
        }
    }

    br {
        display: none;
    }
}
