@import "./parts/circleBoxItem/appe_galleryCircleItem";
@import "./parts/circleBoxWrap/circleBoxWrap";

.appe_galleryCirclesSec {
    position: relative;
    &:before {
        position: absolute;
        top: 0;
        right: 50%;
        transform: translate(50%, 50%);
        content: "";
        width: 0;
        height: 2.5px;
        background: $gold;
        height: 60px;
        width: 1px;
        z-index: 1;

        @include pc {
            top: 14%;
            transform: translate(50%, 0%);
        }
    }
}
.appe_galleryCirclesSec_addbk {
    &:after {
        content: "";
        width: 100%;
        height: 100%;
        background: #faf7f1;
        position: absolute;
        bottom: 0;
        left: 0;
        background: linear-gradient(to right bottom, transparent 51%, #faf7f1 51%);
    }
}

// サークル横にくる小さめタイトル（clear等）
.appe_galleryCirclesSec_headingSmall {
    font-family: $baseFont;
    font-weight: $bol;
    font-size: 1.6rem;
    color: $orange;
    letter-spacing: 0.025em;
    text-align: center;
    display: inline-block;
    position: relative;
    text-align: center;
    margin-top: 1.5em;
    margin-bottom: 2em;
    &:after,
    &:before {
        content: "";
        width: 25px;
        height: 1px;
        position: absolute;
        background: $gold;
    }
    // 上
    &:after {
        bottom: 100%;
        left: 100%;
        transform: translateX(-50%) rotate(-45deg);
        transform-origin: left;
    }
    // 下
    &:before {
        top: 130%;
        right: 100%;
        transform: translateX(50%) rotate(-45deg);
        transform-origin: right;
    }
    @include tab {
        margin-bottom: 3em;
    }
}

.appe_galleryCirclesSec_contents {
    padding-top: 7em;
    padding-bottom: 5em;
}
.appe_galleryCirclesSec_contents_flexPC {
    margin-top: 1.5em;

    @include pc {
        display: flex;
        align-items: center;
        max-width: 1100px;
        margin: 0 auto;
        overflow: hidden;
    }
}
.appe_galleryCirclesSec_contents_nonfulContents {
    width: 90%;
    margin: 0 auto;
    position: relative;
    @include pc {
        width: 100%;
    }
}
.appe_galleryCirclesSec_contents_flexPC_item {
    // サークルのタイトルのラップ（clear等 appe_galleryCirclesSec_headingSmallの親）
    &:first-of-type {
        display: flex;
        justify-content: center;
        @include pc {
            text-align: initial;
        }
    }

    @include pc {
        position: relative;
        z-index: 9999;
        &:first-of-type {
            width: 8%;
            padding-bottom: 60px;
            // 文字が画像中央に来ないから
        }
        &:last-of-type {
            width: 92%;
            display: flex;
            justify-content: space-around;
        }
    }
}

.appe_galleryCirclesSec_contents_flexPC_line {
    display: none;
    @include pc {
        display: block;
        height: 1px;
        width: 100%;
        max-width: 1100px;
        background: $gold;
        margin: 3em auto;
        position: relative;
        z-index: 1;
    }
}
