.subGlobalNavWrap {
    border-bottom: 1px solid #f0f0f0;
}
.subGlobalNav {
    display: none;
    @include pc {
        padding: 5px 0;
        display: flex;
        justify-content: center;
        width: 97%;
        max-width: 1318px;
        margin: 0 auto;
    }
}

.subGlobalNav_list {
    list-style-type: none;
    position: relative;
    transition: all 0.3s ease;
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 10px;
    color: #c8a96a;
    line-height: 1.5;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 19px;
    &:last-of-type {
        margin-right: 0;
    }

    &:before {
        content: "";
        color: $deepOrange;
        display: block;
        margin-right: 4px;
        background-image: url("#{$imagePath}/icon/ico_arrow_trian.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        width: 5.5px;
        height: 5px;
    }
    a {
        transition: all 0.3s ease-in-out;
    }
    &:hover {
        color: $gold;
        a {
            color: $gold;
        }
    }
}

.subGlobalNav_list_alink {
    display: block;
    color: $darkTxtCol;
    line-height: 1.2;
}
