@import "../../global/config";

.hamburgerBtnWrap {
    width: 35px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 100%;
    }

    @include pc {
        &:after {
            display: none;
        }
    }
}
.hamburgerBtn {
    width: 20px;
    height: 20px;
    cursor: pointer;
    position: relative;
    @include pc {
        display: none;
    }
}

.hamburgerBtn_sizing {
    width: 100%;
    height: 100%;
}

.hamburgerBtn_line {
    width: 100%;
    height: 1px;
    content: "";
    background-color: black;
    transition: all 0.25s ease-in;
    position: absolute;
    left: 0;
    &:nth-of-type(1) {
        top: 0;
    }
    &:nth-of-type(2) {
        top: 50%;
        // transform: translateY(-50%);
    }
    &:nth-of-type(3) {
        bottom: 0;
    }
    &--active {
        &:nth-of-type(1) {
            transform: translateY(-50%) rotate(-45deg);
            top: 50%;
        }
        &:nth-of-type(2) {
            opacity: 0;
        }
        &:nth-of-type(3) {
            transform: translateY(50%) rotate(45deg);
            bottom: 50%;
        }
    }
}
