.template_c_contents_section_txtWithImage {
    position: relative;
    width: 318px;

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid #c8a96a;
        transform: translate(16px, -16px);
        box-sizing: border-box;
    }

    img {
        position: relative;
        z-index: 1;
    }

    @include pc {
        width: 400px;
        margin-right: 16px;

        &:before {
            transform: translate(20px, -20px);
        }
    }
}

.template_c_contents_section_baseText_inner {
    @include pc {
        margin-top: 90px;
    }
}

.template_c_contents_section_baseText {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 2;
    min-height: 277px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include pc {
        display: block;
        font-size: 15px;
    }

    div {
        p {
            margin-top: 30px;
            &:first-of-type {
                margin-top: 22px;
            }
        }

        // 画像
        &:nth-child(1) {
            order: 1;
            margin-top: 45px;
            margin-bottom: 30px;

            @include pc {
                float: right;
                margin-top: initial;
                margin-bottom: initial;
                margin-left: 40px;
                margin-top: 10px;
            }
        }

        &:nth-child(2) {
            order: 2;
        }
    }
}

.template_c_contents_section_baseText_noFloat {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 2;
    min-height: 277px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include pc {
        font-size: 15px;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;

        margin-top: 70px;
    }

    div {
        p {
            margin-top: 30px;
            &:first-of-type {
                margin-top: initial;
            }
        }

        &:nth-child(1) {
            margin-bottom: 50px;

            @include pc {
                width: 340px;
                margin-bottom: initial;
                // margin-left: 40px;
                // margin-top: 10px;
            }
        }
        // 画像
        &:nth-child(2) {
            order: 2;
        }
    }

    .template_c_contents_section_txtWithImage {
        @include pc {
            margin-right: initial;
        }
    }
}

.template_c_contents_section_baseText_inner_noFloat {
}
