// slick-slideの中に入ってる
// した２つをラップ
.appe_galleryCircleItem {
    margin: 0 5px;
    max-width: 150px;

    @include tab {
        max-width: 300px;
        margin: 0 15px;
    }

    @include pc {
        margin: 0 30px;
        max-width: initial;
        width: 270px !important;
    }
}

// 画像をラップ
.appe_galleryCircleItem_imageWrap {
    width: 100%;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 2em;
    @include pc {
        margin-bottom: 4em;
    }
}

.wrap_img_and_shadow {
    position: relative;
}

.shadow {
    position: absolute;
    bottom: -15%;
    right: 50%;
    transform: translate(50%, 0%);

    @include pc {
        // bottom: -10%;
    }
}
