.appe-SquareBoxesSmall-item {
    width: 47%;
    margin-bottom: 1em;
    max-width: 200px;
    @include pc {
        width: 30%;
        max-width: initial;
        display: flex;
        flex-direction: column;
    }
}

.appe-SquareBoxesSmall-item_imageWrap {
    margin-bottom: 0.5em;
    img {
        border-radius: 5px;
    }
}
.appe_description {
    @include pc {
        font-family: $baseFont;
        font-weight: $reg;
        font-size: 1.3rem;
        color: $darkTxtCol;
        letter-spacing: 0.025em;
        line-height: 1.3846153846153846;
        margin-top: 0.5em;
        order: 3;
    }
}
