.template_f_contents_section_txtWithImage {
    position: relative;
    width: 318px;

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid #c8a96a;
        transform: translate(16px, -16px);
        box-sizing: border-box;
    }

    img {
        position: relative;
        z-index: 1;
    }

    @include pc {
        width: 400px;
        margin-right: 16px;

        &:before {
            transform: translate(20px, -20px);
        }
    }
}

.template_f_contents_section_baseText {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 2;
    min-height: 277px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include pc {
        display: block;
        margin-top: 90px;
        font-size: 15px;
    }

    div {
        p {
            margin-top: 30px;
            &:first-of-type {
                margin-top: initial;
            }
        }

        // 画像
        &:nth-child(1) {
            order: 2;
            margin-top: 45px;
            margin-bottom: 30px;

            @include pc {
                float: right;
                margin-top: initial;
                margin-bottom: initial;
                margin-left: 40px;
                margin-top: 10px;
            }
        }

        &:nth-child(2) {
            // background: yellow;
            order: 1;
        }

        &:nth-child(3) {
            // background: yellow;
            order: 3;
            @include pc {
                margin-top: 30px;
            }
        }
    }
}

.template_f_contents_section_baseText_last {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 2;
    margin-top: 35px;
    margin-bottom: 80px;

    @include pc {
        margin-bottom: 0;
        font-size: 15px;
        margin-top: 30px;
    }

    &--bold {
        display: none;
        @include pc {
            display: block;
            font-weight: $bol;
            a {
                text-decoration: underline;
            }
        }
    }
}

.template_f_contents_link_listWrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: ease-in height 0.25s;
    z-index: 1000;
    background: rgba(255, 255, 255, 0.8);
    display: none;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.template_f_contents_link_list {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    background: #fff;
    // transition: all ease-in 1s;
    display: none;
}

.template_f_contents_link {
    background-color: $bkLightbeige;
    background-color: transparent;
    padding-bottom: 60px;
    position: relative;

    @include pc {
        display: none;
    }

    &::before {
        content: "";
        position: absolute;
        top: -60px;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $bkLightbeige;
        transform: skewY(-13deg);
        z-index: -1;
    }

    &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 90%;
        bottom: 0;
        right: 0;
        background-color: $bkLightbeige;
        z-index: -1;
    }
}

.template_f_contents_link_list_item_alink {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 12px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 1.3333333333333333;
    display: block;
    border-bottom: 1px solid #f0f0f0;
    box-sizing: border-box;
    display: flex;
    align-content: center;
    justify-content: space-between;
    height: 50px;
    background: #fff;
}

.template_f_contents_link_list_item_alink_txt {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 76%;
    margin: 0 auto;
}

.template_f_contents_link_btnWrap {
    width: 80%;
    max-width: 254px;
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 29.5px;
}

.template_f_contents_link_list_btnWrap {
    width: 80%;
    max-width: 125px;
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 29.5px;
}

#openRelatedPageNavOpen,
#openRelatedPageNavClose {
    cursor: pointer;
}

