.template_f_contents_accredited_org {
    font-family: $baseFont;
    color: $darkTxtCol;
    // margin-bottom: 142px;
    @include pc {
        // margin-bottom: 99px;
    }
}

.template_f_contents_doubleList {
    &:last-of-type {
        @include pc {
            margin-top: 60px;
        }
    }
}

.template_f_contents_accredited_org_titleWrap {
    display: flex;
    align-items: center;
    position: relative;

    .template_f_contents_accredited_org_title {
        margin-bottom: initial;
    }

    &--topMargin {
        margin-top: 40px;
    }

    &:after {
        position: absolute;
        content: "";
        width: 100%;
        height: 2px;
        background-color: $baseGreen;
        bottom: 0;
        left: 0;
    }
}

.template_f_contents_accredited_org_title {
    color: $baseGreen;
    font-weight: bold;
    margin-bottom: 26px;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    line-height: 1.3;

    @include pc {
        font-size: 20px;
        margin-bottom: 31px;
    }
}

.template_f_contents_accredited_org_title_icon {
    margin-right: 10px;

    @include pc {
        margin-right: 8.4px;
    }
}

.template_f_contents_accredited_org_list {
    background-color: #f7f7f7;
    padding: 25px 20px;
    max-width: 334px;
    margin: 0 auto;
    margin-top: 14px;

    @include pc {
        max-width: initial;
        display: flex;
        align-items: center;
        padding: 35px 0 35px 0;
        margin-top: 15px;
    }

    &--top_space {
        @include pc {
            margin-top: 25px;
        }
    }

    &--whiteBk {
        background-color: #fff;
    }
}

.template_f_contents_accredited_org_list_item {
    margin-left: 1.4em;
    text-indent: -1.6em;
    padding-left: 14px;
    position: relative;
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 1.5;
    margin-bottom: 18px;

    &--widthDesc {
        margin-bottom: 0;
        &:not(:last-of-type) {
            + .template_c_contents_accredited_org_list_txt_innerText_small {
                margin-bottom: 18px;
            }
        }
    }

    &::before {
        content: "";
        width: 6px;
        height: 6px;
        display: inline-block;
        transform: translateY(-2.5px);
        border-radius: 50%;
        margin-right: 14px;
        background-color: #383838;
    }

    a {
        color: $darkTxtCol;
        &:hover {
            text-decoration: underline;
        }
    }

    &:last-of-type {
        margin-bottom: 0;
    }
}

.template_f_contents_accredited_org_list_flagTxImg {
    margin-bottom: 18px;
    @include pc {
        margin: auto 0;
    }
}

.template_f_contents_accredited_org_list_flagTxImgWrap {
    width: 60px;
    margin: 0 auto;
    @include pc {
        padding-left: 55px;
        padding-right: 55px;
    }
}

.template_f_contents_accredited_org_list_flagTxImg_txt {
    text-align: center;
    font-family: $baseFont;
    font-weight: $bol;
    font-size: 16px;
    color: #cc813b;
    letter-spacing: 0.025em;
    margin-top: 10px;
}

.template_f_contents_accredited_org_list_txt {
    @include pc {
        padding-right: 2.5em;
    }
}
