.appe_hero {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    padding-top: 250px;
    position: relative;
    overflow: hidden;
    padding-bottom: 3em;
    @include pc {
        padding-bottom: initial;
        padding-top: 10rem;
        justify-content: center;
    }
}

.appe_hero_bk_img {
    position: absolute;
    left: 0;
    top: 0;
    width: 250px;
    left: -20%;
    top: -10%;
    @include pc {
        width: 600px;
        left: -180px;
        top: -40%;
    }
}

.appe_heroHeadingWrap {
    background-color: $baseGreen;
    width: 100%;
    padding: 1.5em 0;
    position: relative;
    @include pc {
        background-color: transparent;
        .single_heroHeader_heading {
            color: $baseGreen !important;
        }
    }
}

.appe_heroHeadingWrap_imgWrap {
    max-width: 234px;
    position: absolute;
    right: 0;
    bottom: 10%;
    z-index: 0;
    img {
        position: relative;
        z-index: 1;
    }
    &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        transform: translate(10px, -10px);
        top: 0;
        left: 0;
        z-index: 0;
        border: solid $gold 1px;
    }
}

.iconWrap {
    width: 40px;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0, -125%);
    @include pc {
        transform: translate(0, -250%);
    }
}

.appe_hero_txt {
    padding: 2em 0;
}

.appe_WrapPC {
    z-index: 1;
    position: relative;
    @include pc {
        width: 90%;
        margin: 0 auto;
        max-width: 1100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.appe_WrapPC_item {
    color: $darkTxtCol;
    @include pc {
        // テキスト
        &:nth-of-type(1) {
            width: 50%;
        }
        // 画像
        &:nth-of-type(2) {
            right: 0;
            top: 0;
            width: 50%;
        }
    }
}

.appe_containerResetPC {
    @include pc {
        width: initial;
        max-width: initial;
    }
}

.appe_heroHeadingWrap_imgWrap_onlyPC {
    @include pc {
        img {
            z-index: 1;
            width: initial;
            height: initial;
            width: 100%;
        }
        &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            transform: translate(2.5%, -2.5%);
            top: 0;
            left: 0;
            z-index: -1;
            border: solid $gold 1px;
        }
    }
}

.appe_single_heroHeader_heading {
    @include pc {
        width: 120% !important;
    }
}
