.single_heroHeader {
    height: 30vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    margin-bottom: 65px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include pc {
        height: 40vh;
        margin-bottom: 70px;
    }

    &--pageWithSideNav {
        @include pc {
            margin-bottom: initial;

            .single_heroHeader_verticalLine {
                display: none;
            }
        }
    }

    &--halfHeight {
        height: 25vh;
    }

    &--PC {
        display: none;
        @include pc {
            display: flex;
        }
    }

    &--Mobile {
        display: flex;
        @include pc {
            display: none;
        }
    }

    // Fページの分岐
    &--UnderMargin {
    }

    &--contractors_developers {
    }

    &--code_professionals_consultants {
        @include pc {
            background-position: 0% 50%;
        }
    }

    &--architects {
        @include pc {
            background-position: 25% 80%;
        }
    }

    &--siding_decking_panel_stainers_home_shed_manufacturers {
        @include pc {
            background-position: 0% 50%;
        }
    }

    &--diy_home_cottage_owners_ranchers {
        @include pc {
            background-position: 25% 60%;
        }
    }

    &--lumberyards_distributors {
        @include pc {
            background-position: 25% 50%;
        }
    }

    &--log_homes_timber_framers {
        @include pc {
            background-position: 0% 40%;
        }
    }

    &--scaffolding_forming_pallets_boxes_poles_bridge_timbers {
    }

    &--first_nations {
        @include pc {
            background-position: 0% 50%;
        }
    }

    &--certifications_tests {
        @include pc {
            background-position: 0% 0;
        }
    }
}
