.faq_heroHeader {
    background-image: url("#{$imagePath}/FAQ-page/hero_image_faqs_2x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @include pc {
        background-image: url("#{$imagePath}/FAQ-page/pc/hero_image_faqs_2x.jpg");
    }
}

.faq_title {
    font-family: $secFont;
    font-weight: bold;
    color: $baseGreen;
    text-align: center;
    font-size: 28px;
    position: relative;
    margin-bottom: 60px;
    letter-spacing: 0.02em;

    @include pc {
        font-size: 32px;
    }
}
