@import "./pdf_popUp/pdfPopup";

.pdf_page_contents_heading {
    color: $baseGreen;
    font-size: 2.6rem;

    font-family: $secFont;
    font-weight: $bol;
    font-size: 26px;
    color: $baseGreen;
    letter-spacing: 0.025em;
}

.pdf_page_contents_list {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 1.4rem;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    margin-top: 34px;
    line-height: 2;

    @include pc {
        margin-top: 40px;
        font-size: 1.5rem;
        margin-left: 1em;
    }

    &--on_C_page {
        margin-top: 0;
    }
}
.pdf_page_contents_list_item {
    &:not(:first-of-type) {
        margin-top: 22px;

        @include pc {
            margin-top: 8px;
        }
    }

    &--on_H_page {
        &:not(:first-of-type) {
            margin-top: 40px;
        }

        @include pc {
            margin-top: 8px !important;
        }
    }
    &--on_C_page {
        &:not(:first-of-type) {
            margin-top: 19px;
        }

        @include pc {
            margin-top: 8px !important;
        }
    }
}

.pdf_page_contents_list_item_link {
    text-decoration: underline;
    transition: 0.25s ease all;

    @include pc {
        &:hover {
            text-decoration: none;
            color: $gold;
        }
    }
}

.pdf_page_contents_list_item_iconWrap {
    display: inline-block;
    vertical-align: text-bottom;
    margin-left: 0.5em;
    line-height: 0;
    width: 20px;
}
