.pdf_popUp_bk {
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;

    display: none;
}
.pdf_popUp {
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    background: #fff;
    width: 90%;
    max-width: 334px;
    border-radius: 5px 5px 5px 5px;

    @include pc {
        max-width: 574px;
    }
}

.pdf_popUp_title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    @include pc {
        margin-top: 52px;
        flex-direction: row;
    }
}
.pdf_popUp_title_heading {
    font-family: $secFont;
    font-weight: $bol;
    font-size: 26px;
    color: $baseGreen;
    letter-spacing: 0em;
    text-align: center;
    padding-top: 20px;

    @include pc {
        padding-top: initial;
        margin-left: 19px;
    }
}

.pdf_popUp_text {
    font-family: $baseFont;
    font-size: 14px;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    line-height: 1.7142857142857142;
    margin-top: 14px;
    margin-bottom: 30px;
    text-align: center;

    @include pc {
        margin-top: 24px;
        margin-bottom: 39px;
        font-size: 15px;
    }
}

// icon
.pdf_popUp_title_iconWrap {
    width: 46px;
    margin: 0 auto;

    @include pc {
        margin: initial;
    }
}

.pdf_popUp_btnWrap {
    display: flex;
    justify-content: center;
    align-content: center;
    margin-bottom: 40px;

    @include pc {
        margin-bottom: 55px;
    }
}
// btn

a {
    .pdf_popUp_btnWrap_btn {
        margin-left: 18px;
        font-size: 13px;
        @include pc {
            margin-right: 22px;
        }
    }
}
.pdf_popUp_btnWrap_btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    padding: 0;
    appearance: none;
    //
    padding: 20px 0;
    min-width: 120px;
    border-radius: 20px;
    display: inline-block;
    font-family: $baseFont;
    font-weight: $mid;
    font-size: 14px;
    color: #ffffff;
    letter-spacing: 0.025em;
    line-height: 0;
    box-sizing: border-box;

    @include pc {
        min-width: 162px;
    }

    &--cancel {
        color: $baseGreen;
        border: 1px solid $baseGreen;

        &:active {
            background: $baseGreen;
            color: #fff;
        }
    }
    &--open {
        border: 1px solid $baseGreen;
        background: $baseGreen;
        &:active {
            background: #fff;
            color: $baseGreen;
            border: 1px solid $baseGreen;
            box-sizing: border-box;
        }
    }
}
