.template_g_contents_contents {
    margin-bottom: 50px;
    margin-top: 30px;

    @include pc {
        margin-top: 80px;
        margin-bottom: 70px;
    }
}

.template_g_contents_contents_wrap_items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .template_b_contents_link_page_single {
        width: 47%;
        height: auto;
        margin-bottom: 30px;

        @include pc {
            width: 23.5%;
            box-shadow: none;
            border: solid 1px #dddddd;
        }
    }

    .template_b_contents_link_page_single:hover {
        @include pc {
            box-shadow: 0px 5px 20px rgb(0 0 0 / 10%);
            transform: translate(5px, -10px);
            transition: ease 0.5s;
            border: solid 1px transparent;
        }
    }

    .template_b_contents_link_page_single_title {
        font-size: 11px;
        line-height: 1.2;

        @include pc {
            font-size: 18px;
            line-height: 1.3;
        }
    }

    .template_b_contents_link_page_single_title_ico {
        @include pc {
            width: 30px;
            height: 30px;
        }
    }
}
