.circleBoxWrap {
    // 内容が２つ以下の時に中央寄せ
    &--lessThanTwo {
        display: flex;
        justify-content: center;
        z-index: 2;
        position: relative;
    }
    @include pc {
        width: 100%;
    }
    .slick-list {
        @include pc {
        }
        .slick-track {
            @include pc {
                width: 100% !important;
                display: flex;
                justify-content: space-between;
                margin: 0 auto;
                &:after,
                &:before {
                    display: none !important;
                }
            }
            .slick-slide {
                min-height: initial !important;
                // @include pc {
                // }
                // スライダー分だけ要素ある
                // div{
                //     .appe_galleryCircleItem{

                //     }
                // }
            }
        }
    }
}
