.hasSubNav {
}

.subMenu {
    display: none;
    @include pc {
        position: absolute;
        top: 100%;
        width: 100vw;
        left: 50%;
        transform: translate(-50%, 0);
        background-color: $bkLightbeige;
        justify-content: center;
        align-items: center;
        z-index: 1;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
    }
}
.subNav_list {
    display: flex;
    @include pc {
        margin-right: 49px;
        padding: 30px 0;
        &:last-of-type {
            margin-right: 0;
        }
    }
}
.subNav_list_alink {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 12px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 1.3333333333333333;
    display: inline-block;
    padding: 17px 0;
    width: 100%;
    background-color: $bkLightbeige;
    border-top: 1px solid #eaeaea;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &--lastBorder {
        border-bottom: 1px solid #eaeaea;
    }

    @include pc {
        padding: initial;
        border-top: initial;
        font-weight: $bol;
        transition: all 0.3s ease-in-out;

        &:hover {
            transform: translateX(5px);
            color: $gold;
            .navarrw_wrapArrow {
                border: 1px solid $gold;
            }
            .navarrw_wrapArrow_innerIcon {
                background-image: url("#{$imagePath}/H-page/ico/ico_arrow_on.svg");
            }
        }

        &--lastBorder {
            border-top: initial;
        }
    }
}

.subNav_list_alink_txt {
    margin-left: 20px;
    width: 100%;
    @include pc {
        margin-left: 0;
    }
}

.navarrw_wrapArrow {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 20px;
    height: 20px;
    border-radius: 20px;
    border: 1px solid #383838;
    overflow: hidden;
    transition: all 0.1s ease-in-out;
    margin-left: 6px;
}
