@import "../../../../../../../node_modules/slick-carousel/slick/slick.scss";
@import "../../../../../../../node_modules/slick-carousel/slick/slick-theme.scss";

.slickWrap {
    margin-top: calc(3em - 0.5em);
    // itemのマージンをマイナス
    .slick-track {
        @include pc {
            width: 100%;
            display: flex;
            flex-direction: column;
            flex-direction: center;
            align-items: center;
        }
    }

    .slick-slide {
        @include pc {
            width: 100% !important;
        }
    }

    .slickWrap_wrapItem {
        @include pc {
            display: flex !important;
            justify-content: space-between;
        }

        &--singleItem {
            &:after {
                content: "";
                width: 100px;
                height: 100px;
            }
        }
    }

    .squareBoxesBig_imageWrap {
        max-width: 538px;
    }
}

.happyVoiceArrow {
    width: 30px;
    height: 50px;
    position: absolute;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
    cursor: pointer;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 30%;
    background-color: rgba(255, 255, 255, 0.7);
    &--prev {
        background-image: url("#{$imagePath}/icon/ico_arrow_greenLeft.svg");
        left: 0;
        border-radius: 0px 5px 5px 0px;
        transition: all 0.5s ease;
    }
    &--nxt {
        background-image: url("#{$imagePath}/icon/ico_arrow_greenRight.svg");
        right: 0;
        border-radius: 5px 0px 0px 5px;
        transition: all 0.5s ease;
    }
    &--relatedPage {
        transform: translate(0, -100%);
    }
}

.slick-disabled {
    opacity: 0;
}

.slick-list {
    .slick-loading & {
        background: initial;
    }
}
