// hero
$whichOne: "hero";

$text-color: $baseGreen;
$back-color: rgba(255, 255, 255, 0.8);
$arrow-color: $baseGreen;
$border-color: $baseGreen;

$hover-txt-color: #fff;
$hover-back-color: $baseGreen;
$hover-arrow-color: #fff;
$hover-border-color: $baseGreen;

$iconMarginRight: 15px;

$paddingTandB: 10px;

@for $i from 1 through 5 {
    @if $i == 2 {
        //aboutUs
        $whichOne: "aboutUs";

        $text-color: #fff;
        $back-color: rgba(0, 0, 0, 0.1);
        $arrow-color: #fff;
        $border-color: #fff;

        $hover-txt-color: $baseGreen;
        $hover-back-color: #fff;
        $hover-arrow-color: $baseGreen;
        $hover-border-color: #fff;

        $iconMarginRight: 15px;

        $paddingTandB: 12.5px;
    } @else if $i == 3 {
        //faq
        $whichOne: "faq";

        $text-color: $baseGreen;
        $back-color: transparent;
        $arrow-color: $baseGreen;
        $border-color: $baseGreen;

        $hover-txt-color: #fff;
        $hover-back-color: $baseGreen;
        $hover-arrow-color: #fff;
        $hover-border-color: $baseGreen;

        $iconMarginRight: 10px;

        $paddingTandB: 12.5px;
    } @else if $i == 4 {
        //freeSemi
        $whichOne: "freeSemi";

        $text-color: $darkTxtCol;
        $back-color: #fff;
        $arrow-color: $darkTxtCol;
        $border-color: #fff;

        $hover-txt-color: #fff;
        $hover-back-color: $gold;
        $hover-arrow-color: #fff;
        $hover-border-color: $gold;

        $iconMarginRight: 10px;

        $paddingTandB: 12.5px;
    } @else if $i == 5 {
        //gall
        $whichOne: "gall";

        $text-color: $baseGreen;
        $back-color: transparent;
        $arrow-color: $baseGreen;
        $border-color: $baseGreen;

        $hover-txt-color: #fff;
        $hover-back-color: $baseGreen;
        $hover-arrow-color: #fff;
        $hover-border-color: $baseGreen;

        $iconMarginRight: 10px;

        $paddingTandB: 12.5px;
    }

    .front_btn {
        font-family: $baseFont;
        letter-spacing: 0.04em;
        display: inline-block;

        border-radius: 50px;
        display: flex;
        justify-content: center;
        align-content: center;
        transition: all 0.25s ease-in;

        font-weight: $reg;
        font-size: 14px;

        &--#{$whichOne} {
            padding: $paddingTandB 30px;

            background: $back-color;
            // オリジナルボーダー
            border: 1px solid $border-color;

            .front_btn_ico {
                margin-left: $iconMarginRight;
            }
            @include pc {
                &:hover {
                    // ホバー背景
                    border: 1px solid $hover-border-color;
                    background-color: $hover-back-color;

                    .front_btn_ico {
                        &:after {
                            // ホバー矢印の先
                            width: 100%;
                            background: $hover-arrow-color;
                        }
                    }

                    .front_btn_ico_edge {
                        // ホバー矢印の先端
                        &:before,
                        &:after {
                            background-color: $hover-arrow-color;
                        }
                    }

                    // ホバーテキストカラー
                    .front_btn_txt {
                        color: $hover-txt-color;
                    }
                }
            }
        }

        @include pc {
            &:hover {
                .front_btn_ico {
                    transform: translateX(2px);
                    &:after {
                        width: 100%;
                    }
                }
                .front_btn_txt {
                    transform: translateX(2px);
                    position: relative;
                }
            }
        }
    }

    .front_btn_txt {
        transition: all ease 0.5s;
        &--#{$whichOne} {
            // ノーマルテキスト
            color: $text-color;
        }
    }

    .front_btn_ico {
        transition: all 0.25s ease-in;
        width: 10.5px;
        position: relative;
        display: flex;
        align-items: center;
        position: relative;
        // margin-left: 7px;
        &:after {
            border-radius: 10px;
            transition: all 0.25s ease-in;
            display: block;
            content: "";
            width: 50%;
            height: 2px;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }

        &--#{$whichOne} {
            &:after {
                // ノーマルの矢印の先
                background-color: $arrow-color;
            }
        }
    }

    .front_btn_ico_edge {
        position: absolute;
        width: 7px;
        height: 7px;
        top: 50%;
        right: 0;
        transform: translate(20%, -50%) rotate(45deg);

        &:before,
        &:after {
            border-radius: 50px;
            position: absolute;
            transition: all 0.25s ease-in;
            content: "";
        }

        &--#{$whichOne} {
            // ノーマルの矢印の先
            &:before,
            &:after {
                background-color: $arrow-color;
            }
        }

        &:before {
            width: 100%;
            height: 2px;
        }

        &:after {
            width: 2px;
            height: 100%;
            right: 0;
        }
    }
}
