.front_hero {
    position: relative;
    height: 70vh;
    background-image: url("#{$imagePath}/front-page/front-hero/bg_hero_01_2x.jpg");
    background-position: 70% top;
    background-repeat: no-repeat;
    background-size: cover;
    line-height: 1.3846153846153846;
    overflow: hidden;
    &:after {
        position: absolute;
        content: "";
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 5%;
        background: $bkLightbeige;
        z-index: 0;
    }

    @include pc {
        height: 85vh;
    }
}
.front_hero_contents {
    position: absolute;
    transform: translate(0, -50%);
    top: 50%;
    left: 0;
    @include pc {
        top: 45%;
    }
}

.front_hero_contents_txt {
    font-family: $baseFont;
    color: $baseGreen;
    font-weight: $bol;
    text-shadow: $whiteShadow;
}

.front_hero_contents_txt_big {
    font-size: 2.4rem;
    line-height: 30px;
    @include pc {
        // font-size: 5rem;
        font-size: 37px;
        line-height: 1.135135135135135;
        margin-bottom: 20px;
    }
}

.front_hero_contents_txt_small {
    font-size: 1.25rem;
    margin-top: 2rem;
    @include pc {
        margin-top: 1rem;
        font-size: 20px;
    }
}

.hero_maskWrap {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}

.front_hero_contents_btnWrap {
    width: 182px;
    margin-top: 1.25em;
    position: relative;
    @include pc {
        margin-top: 3em;
    }
}
