@import "../pageNation/pageNation";

// header
.whatsNew_header {
    display: flex;
    flex-direction: column;
}

// section
.whatsNew_archive {
    margin-bottom: 50px;

    @include pc {
        margin-bottom: 90px;
    }
}

.whatsNew_header_txt {
    font-family: $secFont;
    text-align: center;
    color: $baseGreen;
    font-weight: $bol;
    font-size: 26px;
    letter-spacing: 0.05em;
    position: relative;
    margin-top: 45px;
    margin-bottom: 60px;
    line-height: 1.7692307692307692;

    @include pc {
        font-size: 41px;
        margin-top: 50px;
        margin-bottom: 74px;
    }

    &::after {
        content: "";
        background: #c8a96a;
        width: 40px;
        height: 1px;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 14px);

        @include pc {
            transform: translate(-50%, 20px);
        }
    }
}

.whatsNew_header_bg_img {
    width: 250px;
    position: absolute;
    bottom: -70px;
    left: -130px;
    @include pc {
        width: 500px;
        bottom: -180px;
        left: -100px;
    }
}

// content
.whatsNew_article_single {
    font-family: $baseFont;
    border-bottom: solid 1px $gold;
    display: flex;
    padding: 25px 0;
    transition: background-color 0.4s ease-in-out;
    @include pc {
        padding: 20px 30px 20px 30px;

        &:hover {
            background: #fcf8f5;
        }
    }
}

.whatsNew_article_img {
    width: 50%;
    height: auto;
    margin-right: 25px;
    max-width: 164px;
    min-width: 164px;

    @include pc {
        margin-right: 37px;
    }

    img {
        border-radius: 0.25em;
    }
}

.whatsNew_article_txt {
    color: $darkTxtCol;
    width: 100%;
}

.whatsNew_article_txt_date {
    font-weight: 100;
    font-family: $baseFont;
    font-weight: 300;
    font-size: 12px;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    margin-bottom: 7px;

    @include pc {
        margin-bottom: 12px;
    }
}

.whatsNew_article_txt_title {
    font-weight: bolder;
    padding-right: 10px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;

    font-family: $baseFont;
    font-weight: $bol;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 1.4285714285714286;

    @include pc {
        font-size: 15px;
        line-height: 1.5;
    }
}
