@import "./1topSection";
@import "./2txtWithImage";
@import "./3accredited";

.sideNavWrap_headingWrap_iconOnly_c {
    width: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(-8px);
}
.template_c_contents {
    margin-top: -20px;
    display: flex;
    flex-direction: column;

    @include pc {
        margin-top: initial;
    }
}

.single_heroHeader_heading_innerC_template {
    font-size: 18px;
    line-height: 1.3333333333333333;
    @include pc {
        font-size: 44px;
        line-height: 1.6363636363636365;
    }
}

.template_c_contents_section {
    &:not(:first-of-type) {
        margin-top: 60px;

        @include pc {
            margin-top: 90px;
        }
    }

    &--noUpperMargin {
        margin-top: 0px !important;
    }

    &--UnderMargin {
        margin-bottom: 150px;

        @include pc {
            margin-bottom: 85px;
        }
    }
}

.template_c_contents_accredited_org_list_txt_innerText {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 2;

    &--btmMargin {
        margin-bottom: 36px;
        @include pc {
            margin-bottom: 28px;
        }
    }
    @include pc {
        font-size: 15px;
    }
}

.template_c_contents_accredited_org_list_txt_innerText_small {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 13px;
    color: #cc813b;
    letter-spacing: 0.025em;
    line-height: 1.5;
    padding-left: 34px;
    display: block;

    @include pc {
        line-height: 1.5 !important;
        margin-top: 3px;
        padding-left: 35px;
    }
}
