.front_bkDiagonalSec_triangle_1 {
    width: 0;
    height: 0;
    border-top: 0 solid transparent;
    border-right: 200vw solid $bkDarkbeige;
    border-bottom: 5vw solid transparent;
    transform: scale(-1, 1);
    position: absolute;
    top: 0;
    @include pc {
        border-bottom: 2vw solid transparent;
    }
}

.front_bkDiagonalSec_triangle_2 {
    width: 0;
    height: 0;
    border-top: 5vw solid transparent;
    border-left: 200vw solid $bkDarkbeige;
    border-bottom: 0 solid transparent;
    transform: scale(-1, 1);
    position: absolute;
    bottom: 0;
    @include pc {
        border-top: 2vw solid transparent;
    }
}

.front_bkDiagonalSec {
    position: relative;
    padding: calc(100vw * 0.12) 0;
    overflow: hidden;
    // background-color: pink;
    &:after {
        content: "";
        background: $bkBeige;
        // background: red;
        width: 100%;
        height: 50%;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 0;
    }
}

.front_bkDiagonalSecBk {
    // background-image: url("#{$imagePath}/front-page/front-bkDiagonal/bg_diagona_mobl_2x.jpg");
    background-image: url("#{$imagePath}/front-page/front-bkDiagonal/bg_diagonal_2x.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    transform-origin: center;
    // transform: rotate(-6.25deg);
    transform: rotate(-3deg);
    width: 200vw;
    height: calc(100% - 0.11 * (0.11 * 50% + 50vw) * 2);
    top: calc(0.11 * (0.11 * 50% + 50vw));
    left: -50vw;
    z-index: 1;

    // outline: 10px solid $bkDarkbeige;
    // outline-offset: -10px;

    @include pc {
        background-size: 52%;
        // background-image: url("#{$imagePath}/front-page/front-bkDiagonal/bg_diagonal_2x.jpg");
    }
}

.front_bkDiagonalSecBk_shadw {
    width: 100%;
    background: linear-gradient(to right, rgba(91, 58, 21, 0.4), rgba(91, 58, 21, 0));
    // opacity: 0.4;
    height: 100%;
    position: relative;
    z-index: -1;

    // rgba(91,58,21,.4),rgba(91,58,21,0)
}
.front_bkDiagonalSecBk_bk {
    position: absolute;
    transform-origin: center;
    transform: rotate(-6deg);
    width: 200vw;
    height: calc(100% - 0.11 * (0.11 * 50% + 50vw) * 2);
    top: calc(0.11 * (0.11 * 50% + 50vw));
    left: -50vw;
    z-index: 1;
    background-color: red;
}
.front_bkDiagonalSec_contentsWrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.front_bkDiagonalSec_contents {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 60%;
    padding: 7rem 0;
    z-index: 1;
    @include pc {
        width: 40%;
    }
}

.front_bkDiagonalSec_btnWrap {
    width: 170px;
    margin-top: 25px;

    @include pc {
        margin-top: 33px;
    }
}

.front_bkDiagonalSec_contents_txt {
    text-shadow: $brownShadow;
    margin-top: 1.2rem;
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 1.4rem;
    color: #fff;
    letter-spacing: 0.025em;
    line-height: 1.4285714285714286;

    @include pc {
        font-size: 15px;
        line-height: 1.3846153846153846;
        margin-top: 23px;
    }
}

