.headerMianContent {
    background: #fff;
    display: flex;
    justify-content: space-between;
    height: 70px;
    position: relative;
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 3;
    &--onlyFront {
        box-shadow: 0px 1px 10px -5px rgba(0, 0, 0, 10);
        @include pc {
            padding-left: 1.5em;
            padding-right: 1.5em;
            border-radius: 10px;
            box-shadow: 0px 1px 10px -5px rgba(0, 0, 0, 0.5);
        }
    }

    @include pc {
        max-width: 1366px;
        height: 90px;
    }

    &--open {
        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background-color: transparent;
            box-shadow: 0px 0px 10px rgb(0 0 0 / 10%);
            pointer-events: none;
        }
        @include pc {
            max-width: 1366px;
            height: 90px;
            &:after {
                display: none;
            }
        }
    }
}

.headerMianContent_left,
.headerMianContent_rigth {
    width: 45%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.headerMianContent_left {
    max-width: 160px;
    margin-left: 4.5%;
    @include pc {
        margin-left: 50px;
    }
}
.headerMianContent_rigth {
    max-width: 150px;
    margin-right: 4.5%;
    @include pc {
        display: none;
    }
}
.logoWrap {
    padding-top: 0.5em;
    width: 120px;
    @include pc {
        width: 140px;
    }
}
