.template_b_contents {
    display: flex;
    flex-direction: column;

    @include pc {
        &--spaceBottomPc {
            margin-bottom: 100px;
        }
    }
}
// header
.template_b_header {
    position: relative;
    overflow: hidden;
    padding-top: 340px;
    &--marginBtm {
        margin-bottom: 54px;
    }
    @include pc {
        padding-top: initial;
        margin-bottom: initial;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        min-height: 540px;
        overflow: initial;
        padding-top: 100px;
        padding-bottom: 28px;
    }
}

.template_b_contents_heading_container {
    position: relative;
    width: 100%;

    @include pc {
        max-width: 368px;
        padding-right: 70px;
        padding-top: 65px;
    }
}

// tree bg
.template_b_header_bg_img {
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-50%, -35%);
    width: 250px;
    z-index: -1;
    opacity: 0.7;

    @include pc {
        width: 500px;
        top: 400px;
        left: -60px;
    }
}

.template_b_contents_heading_title_sideImageWrap {
    width: 235px;
    position: absolute;
    right: 0;
    bottom: 0;
    transform: translateY(-15px);
    z-index: -1;

    img {
        position: relative;
    }

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid $gold;
        transform: translate(10px, -10px);
        @include pc {
            transform: translate(20px, -20px);
        }
    }

    &::after {
        content: "";
        position: absolute;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, transparent 100%);
        mix-blend-mode: multiply;
        bottom: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 60%;

        @include pc {
            display: none;
        }
    }

    @include pc {
        min-width: 438px;
        position: relative;
        z-index: 0;
        transform: initial;
        margin-top: 10px;
    }
}

.template_b_contents_heading_title {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    bottom: 100%;

    &::after {
        content: "";
        background-color: $baseGreen;
        width: 100%;
        height: 100%;
        left: 0;
        position: absolute;

        @include pc {
            display: none;
        }
    }

    @include pc {
        white-space: nowrap;
        position: relative;
    }
}

.template_b_contents_heading_title_innerText_icoWrap {
    width: 40px;
    height: 40px;
    z-index: 1;
    position: absolute;
    bottom: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(25%);
    @include pc {
        transform: translateY(-50%);
    }
}

.template_b_contents_heading_title_innerText {
    font-family: $secFont;
    color: #fff;
    font-weight: $bol;
    font-size: 2em;
    line-height: 1.3em;
    width: 90%;
    margin: 0 auto;
    z-index: 1;
    padding-top: 20px;
    padding-bottom: 20px;
    position: relative;

    @include pc {
        color: $baseGreen;
        font-size: 56px;
        width: initial;
        padding: initial;
    }
}

.template_b_contents_heading_container_description {
    font-weight: $reg;
    font-size: 14px;
    color: #383838;
    letter-spacing: 0.025em;
    line-height: 1.7142857142857142;
    font-family: $baseFont;
    color: $darkTxtCol;
    width: 90%;
    margin: 0 auto;
    padding-top: 20px;

    @include pc {
        font-size: 15px;
        margin: initial;
        width: initial;
        padding-top: 36px;
        line-height: 2;
    }

    p {
        &:not(:first-of-type) {
            margin-top: 26px;
            @include pc {
                margin-top: 29px;
            }
        }
    }
    small {
        font-weight: $reg;
        font-size: 12px;
        color: #383838;
        letter-spacing: 0.025em;
        line-height: 2;

        @include pc {
            font-size: 13px;
            color: #383838;
            letter-spacing: 0.025em;
            line-height: 2.3076923076923075;
        }
    }
}

// product feature
.template_b_contents_product_feature {
    font-family: $baseFont;
    color: $darkTxtCol;

    @include pc {
        margin-bottom: 99px;
    }
}

.template_b_contents_product_feature_title {
    color: $baseGreen;
    font-weight: bold;
    margin-bottom: 24px;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    line-height: 1.3;

    @include pc {
        font-size: 20px;
        margin-bottom: 41px;
    }
}

.template_b_contents_product_feature_list {
    counter-reset: li;

    li {
        margin-bottom: 30px;
        line-height: 1.75em;
        color: $darkTxtCol;
        position: relative;
        padding-left: 30px;
        font-size: 14px;
        letter-spacing: 0.01em;

        &:last-of-type {
            margin-bottom: 0;
        }

        @include pc {
            font-size: 15px;
            margin-bottom: 25px;
        }
    }

    li:before {
        position: absolute;
        top: 0;
        left: 0;
        font-weight: bold;
        color: $baseGreen;
        counter-increment: li;
        content: counter(li) ".";
    }

    @include pc {
        width: 90%;
        margin-left: 5px;
    }
}

// link / related pages
.template_b_contents_link {
    background-color: $bkLightbeige;
    background-color: transparent;
    padding-bottom: 60px;
    position: relative;

    margin-top: 135px;

    @include pc {
        display: none;
    }

    &::before {
        content: "";
        position: absolute;
        top: -60px;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $bkLightbeige;
        transform: skewY(-13deg);
        z-index: -1;
    }

    &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 50%;
        bottom: 0;
        right: 0;
        background-color: $bkLightbeige;
        z-index: -1;
    }
}

.template_b_contents_link_title {
    color: $darkGreen;
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 50px;
    position: relative;
    font-family: $secFont;
    font-weight: bold;
    &::before {
        content: "";
        background: $gold;
        width: 1px;
        height: 60px;
        position: absolute;
        bottom: 85px;
        right: 50%;
        transform: translate(-50%, 50%);
    }
}

.template_b_contents_link_page {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    overflow: hidden;

    margin-bottom: -10px;

    width: 100%;

    .slick-track {
        display: flex;
        padding: 0 0 30px 0;

        // background-color: red;
        .slick-slide {
            height: auto !important;
            > div {
                height: 100%;
            }
        }
    }

    // for spacing
    .slick-slide {
        margin: 0 13px;
    }

    .slick-list {
        margin: 0 -13px;
    }

    /* the parent */
    .slick-list {
        width: 95%;
        margin: 0 auto;
        overflow: visible;
    }
}

// single link
.template_b_contents_link_page_single {
    // box-shadow: 0px 5px #00000019;
    height: 100%;
    border-radius: 1em;
    overflow: hidden;
    height: 100%;
    display: flex !important;
    flex-direction: column;
    transition: all ease 0.25s;

    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);

    &:active {
        box-shadow: initial;
    }
}

.template_b_contents_link_page_single_title {
    color: $baseGreen;
    font-size: 0.7em;
    display: flex;
    align-items: center;
    line-height: 1.2em;
    height: 100%;
    padding: 13px;
    background-color: #fff;
    font-family: $secFont;
    font-weight: bold;
}

.template_b_contents_link_page_single_title_small {
    font-size: 10px;
    @include pc {
        font-size: initial;
    }
}

.template_b_contents_link_page_single_title_ico {
    display: block;
    width: 18px;
    margin-right: 12px;
}

