.front-bkStraightSec {
    background-image: url("#{$imagePath}/front-page/front-bkStraightSec/front-bkStraightSec_2x.jpg");
    background-position: 0 bottom;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 50px 0;
    position: relative;

    @include pc {
        background-image: url("#{$imagePath}/front-page/front-bkStraightSec/front-bkStraightSec_pc_2x.jpg");
        padding: 70px 0;
        background-position: center center;
    }

    &:before,
    &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 5px;
        left: 0;
    }
    &:before {
        top: 0;
        background: linear-gradient(to right, #efe4ce, #c8a96b);
    }
    &::after {
        bottom: 0;
        background: linear-gradient(to left, #c8a96b, #efe4ce);
    }
}

.front-bkStraightSec_ttl {
    font-weight: bold;
    text-align: center;
    margin-bottom: 30px;

    @include pc {
        font-size: 42px;
        margin-bottom: 20px;
    }

    span {
        font-size: 22px;
        @include pc {
            font-size: 32px;
            line-height: 1.625;
        }
    }
}

.front-bkStraightSec_txt {
    font-family: $baseFont;
    color: #fff;
    text-align: center;
    font-size: 14px;
    letter-spacing: 0.025em;
    line-height: 1.4285714285714286;
    padding: 0 1em;

    @include pc {
        font-size: 15px;
        letter-spacing: 0.025em;
        line-height: 2;
    }
}

.front-bkStraightSec_wrapBtn {
    width: 300px;
    margin: 50px auto;
    margin-bottom: 20px;

    @include pc {
        margin: 40px auto;
        width: 250px;
    }
}
