.front_SecHeading {
    text-align: left;
    font-family: $secFont;
    letter-spacing: 0px;
    font-size: 3.2rem;
    position: relative;
    line-height: 1.2307692307692308;

    &--bold {
        font-weight: $bol;
    }

    &--white {
        color: #ffffff;
        text-shadow: $brownShadow;
    }

    &--onlyWhite {
        color: #ffffff;
    }

    &--green {
        color: $baseGreen;
    }

    &--center {
        text-align: center;
    }

    &--froAbout {
        @include pc {
            font-size: 56px;
        }
    }
    &--froFaq {
        @include pc {
            font-size: 4rem;
        }
    }

    &--footer {
        @include pc {
            font-size: 2.6rem;
            letter-spacing: 0.025em;
        }
    }
}

.front_SecHeading_inner {
    position: relative;
    display: inline-block;
    padding-bottom: 27.5px;
    margin-bottom: 20px;

    &:after {
        content: "";
        width: 50px;
        height: 1px;
        background: $gold;
        position: absolute;
        bottom: 0;
        right: 50%;
        transform: translateX(50%);
    }

    @include pc {
        padding-bottom: 25px;
        margin-bottom: 80px;
    }
}
.front_txt {
    font-size: 1.4rem;
    font-family: $baseFont;
    letter-spacing: 0.35px;
    line-height: 1.4285714285714286;
}
