.appe_fullBox_section {
    position: relative;
    &:before {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 0;
        height: 0;
        border-top: 32vw solid transparent;
        border-right: 100vw solid $bkBeige;
        border-bottom: transparent solid transparent;
    }
}
.appe_fullBox_cointents {
    background-color: #fff;
    border-radius: 10px;
    z-index: 2;
    position: relative;
    padding-top: 2em;
    padding-bottom: 2em;
    box-shadow: $blackLongShadow;
}

.appe_fullBox_cointents_list {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 1.8rem;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    line-height: 1.6666666666666667;
    position: relative;
    width: 90%;
    margin: 0 auto;
    margin-top: 1.25em;
    padding-top: 1.25em;

    // &:after {
    //     font-size: 1.5rem;
    //     content: "(ft)";
    //     position: absolute;
    //     right: 0;
    //     top: -1rem;
    // }
}

.appe_fullBox_cointents_list_item {
    padding: 1em 0.5em;
    margin: 2px 0;
    text-align: center;
    background-color: $bkGray;
    @include tab {
        padding: 1em 1em;
    }
}
