.sectionAndSideNavWrap {
    @include pc {
        display: flex;
    }
}

// sideNav そのもの
.sideNavWrap {
    display: none;
    @include pc {
        background: #f5e5d7;
        display: block;
        max-width: 234px;
        min-width: 234px;
    }
}

.fixWrap {
    &--active {
        position: fixed;
        top: 0;
    }
}

.sideNavWrap_headingWrap {
    @include pc {
        padding: 15px 0;
        background-color: $deepOrange;
        &--second {
            background: #fff;
        }
        &--onlyC {
            display: block;
            transition: all 0.25s ease-in-out;
            &:hover {
                background-color: #e0b48a;
                .sideNavWrap_heading--onlyC {
                    transform: translateX(-2.5px);
                    text-decoration: underline;
                }
            }
        }
    }

    a {
        &:hover {
            text-decoration: underline;
        }
    }
}
.sideNavWrap_heading {
    @include pc {
        width: calc(100% - 40px);
        margin: 0 auto;
        font-family: $baseFont;
        font-weight: $bol;
        font-size: 15px;
        color: #ffffff;
        letter-spacing: 0.025em;
        line-height: 1.3333333333333333;

        &--second {
            color: $deepOrange;
        }
    }
    a {
        color: #fff;
        position: relative;
    }

    &--onlyC {
        display: flex;
        align-items: center;
        transition: all 0.25s ease-in-out;
    }
}

.sideNav {
    @include pc {
        // width: calc(100% - 40px);
        // margin: 0 auto;
    }
}

.sideSpace {
    @include pc {
        width: calc(100% - 40px);
        margin: 0 auto;
    }

    &--widhFlex {
        transition: all 0.3s ease;
        display: flex;
        justify-content: space-between;
        align-items: center;
        line-height: 1.4;
    }
}

.sideNav_list {
    padding-bottom: 34.5px;
    &:first-of-type {
        border-bottom: 1px solid $deepOrange;
        margin-bottom: 23.5px;
    }

    &--noSpace {
        border-bottom: initial !important;
        margin-bottom: initial !important;
    }

    &--active {
        background: red;
    }
}

.sideNav_list_item {
    @include pc {
        font-family: $baseFont;
        font-weight: $bol;
        font-size: 12px;
        color: $darkTxtCol;
        letter-spacing: 0.025em;
        line-height: 1.5;
        display: inline-block;
        padding: 1.1em 0;
        transition: all 0.3s ease;

        &:hover {
            color: $gold;
            text-decoration: underline;
        }

        &--full {
            display: flex;
            align-items: center;
            justify-content: space-between;
            transition: initial;
            &:hover {
                background: #fbf5ef;
                .sideSpace--widhFlex {
                    transform: translateX(5px);
                    .sideNav_list_icon {
                        border-color: $gold;
                        .sideNav_list_icon_arrow_wrapIcon {
                            background-image: url("#{$imagePath}/H-page/ico/ico_arrow_on.svg");
                        }
                    }
                }
            }
        }
    }
}

// .sideNav_list_subList{}

.sideNav_list_subList_item {
    @include pc {
        font-family: $baseFont;
        font-weight: $reg;
        font-size: 11px;
        color: $darkTxtCol;
        letter-spacing: 0.025em;
        line-height: 1.6363636363636365;
        padding: 7px 0;
        display: block;
        &--first {
            padding-top: 3px;
        }
    }
}

.sideNav_list_item_and_sideNav_list_iconWrap {
    @include pc {
        &:nth-child(odd) {
            background: #f7eadf;
        }
        &:nth-child(even) {
            background: #f5e5d7;
        }
        // さいどナビがアクティブのときの色
        &--active {
            background: #fff !important;
            .sideNav_list_item--full {
                color: $gold;
                text-decoration: initial;
                .sideNav_list_icon {
                    visibility: hidden;
                }
                &:hover {
                    background: #fff;
                    .sideSpace--widhFlex {
                        transform: initial;
                        .sideNav_list_icon {
                            border-color: initial;
                            .sideNav_list_icon_arrow_wrapIcon {
                                background-image: url("#{$imagePath}/H-page/ico/ico_arrow_on.svg");
                            }
                        }
                    }
                }
            }
        }
    }
}

.sideNav_list_subList_item {
    @include pc {
        position: relative;
        padding-left: 14px;
        transition: all 0.3s ease;

        &:before {
            content: "\25B8";
            left: 0;
            position: absolute;
            color: $deepOrange;
        }

        &:hover {
            color: $gold;
            text-decoration: underline;
        }
    }
}

.sideNav_list_icon {
    @include pc {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        min-width: 18px;
        height: 18px;
        border-radius: 20px;
        // box-sizing: border-box;
        border: 1px solid $darkTxtCol;
        overflow: hidden;
        transition: all 0.1s ease;
    }
}

.sideNav_list_icon_arrow {
    @include pc {
        position: relative;
        color: #000;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.sideNav_list_icon_arrow_wrapIcon {
    width: 10px;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("#{$imagePath}/H-page/ico/ico_arrow.svg");
    transition: all 0.15s ease;
}
