.front_whats_new {
    background-color: $bkLightbeige;
    padding-bottom: 30px;
    @include pc {
        padding-bottom: initial;
        margin-top: initial;
        height: 30px;
        position: relative;
        z-index: 3;
    }
}

.front_whats_new_container {
    @include pc {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -65%);
        display: flex;
        background-color: rgba(255, 255, 255, 0.7);
        border-radius: 10px;
        padding-top: 22px;
        padding-left: 50px;
        padding-right: 70px;
        padding-bottom: 22px;
        display: flex;
        justify-content: space-between;
    }

    &--numOne {
        @include pc {
            top: initial;
            top: 0;
            left: 50%;
            transform: translate(-50%, -75%);
        }
    }
    &--numTwo {
        @include pc {
            top: initial;
            top: 0;
            left: 50%;
            transform: translate(-50%, -75%);
        }
    }
}
.front_whats_new_ttl_wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;

    @include pc {
        margin-bottom: initial;
        padding-top: initial;

        width: 20%;
        max-width: 145px;
        flex-direction: column;
        align-items: flex-start;
    }
}

.front_whats_new_ttl {
    font-family: $secFont;
    color: $baseGreen;
    font-weight: bold;
    font-size: 20px;
    position: relative;
    margin-left: 11px;

    @include pc {
        font-size: 28px;
        line-height: 1.2142857142857142;
        margin-left: initial;
        margin-top: 12.5px;
    }

    &:after {
        content: "";
        width: 40px;
        height: 1px;
        background: $baseGreen;
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(50%, 0);

        @include pc {
            display: none;
        }
    }
}

.front_whats_new_ttl_inner {
    @include pc {
        display: block;
        position: relative;

        &::after {
            content: "";
            content: "";
            width: 40px;
            height: 1px;
            background: $baseGreen;
            position: absolute;
            left: 100%;
            top: 50%;
            transform: translate(-22.5px, -50%);
        }
    }
}

.front_whats_new_ttl_link {
    display: flex;
    align-items: center;
    transition: ease 0.5s;
    margin-right: 11px;
    @include pc {
        margin-right: 22.5px;
        margin-left: auto;
        padding-top: 20px;
        margin-bottom: 20px;
        &:hover {
            transform: translate(5px, 0px);
        }
    }
}

.front_whats_new_ttl_link_txt {
    font-family: $baseFont;
    color: $baseGreen;
    font-size: 14px;
    letter-spacing: 0.025em;
    margin-right: 21px;
    position: relative;

    &:after {
        content: "";
        position: absolute;
        width: 16px;
        height: 16px;
        margin-left: 5px;
        background-image: url("#{$imagePath}/front-page/front-whatsNewBox/front_arrow_green.svg");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    @include pc {
        margin-right: initial;
        &:hover {
            color: $gold;

            &:after {
                background-image: url("#{$imagePath}/front-page/front-whatsNewBox/front_arrow_orange.svg");
            }
        }
    }
}

.front_whats_new_article {
    @include pc {
        width: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

.front_whats_new_article_item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: $baseFont;
    border-bottom: solid 1px $gold;
    padding: 25px 0;

    color: $darkTxtCol;

    @include pc {
        padding: 20px 0;
    }

    &:last-of-type {
        border-bottom: initial;
    }

    &:first-of-type {
        border-bottom: solid 1px $gold;
    }

    @include pc {
        &:hover {
            .front_whats_new_article_item_ttl {
                color: $gold;
            }
        }
    }
}

.front_whats_new_article_item_date {
    font-weight: lighter;
    font-size: 12px;
    letter-spacing: 0.025em;
    margin-left: 11px;
}

.front_whats_new_article_item_ttl {
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 0.025em;
    margin-right: 11px;
    width: 65%;
    letter-spacing: 0.025em;
    line-height: 1.4285714285714286;

    text-overflow: ellipsis;
    overflow: hidden;

    transition: ease 0.4s;
}
