.myGutenberg_page {
    h2 {
        color: $baseGreen;
        font-weight: $bol;
        font-family: $secFont;
        font-size: 2.6rem;
        letter-spacing: 0.025em;
        margin-bottom: 5px;
    }
    p,
    li {
        font-family: $baseFont;
        font-weight: $reg;
        font-size: 1.4rem;
        color: $darkTxtCol;
        letter-spacing: 0.025em;
        line-height: 2.142857142857143;
        @include pc {
            font-size: 1.5rem;
        }
    }
    ul {
        padding-left: 18px;
    }
    strong {
        font-weight: bold;
    }
    time {
        font-family: $baseFont;
        font-weight: 300;
        font-size: 12px;
        color: #383838;
        letter-spacing: 0.025em;
        line-height: 2.3333333333333335;
    }

    figcaption {
        font-size: 1rem;
        font-family: $baseFont;
        color: $darkTxtCol;
    }
}

// ここからポスト
.myGutenberg_post {
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $baseGreen;
        font-weight: $bol;
        font-family: $secFont;
        font-size: 2.6rem;
        letter-spacing: 0.025em;
        margin-top: 8px;

        @include pc {
            margin-top: 3px;
        }
    }
    h3 {
        font-size: calc(2.6rem - 5px);
    }
    h4 {
        font-size: calc(2.6rem - 7px);
    }
    h5 {
        font-size: calc(2.6rem - 9px);
    }
    h6 {
        font-size: calc(2.6rem - 11px);
    }
    p,
    li {
        font-family: $baseFont;
        font-weight: $reg;
        font-size: 1.4rem;
        color: $darkTxtCol;
        letter-spacing: 0.025em;
        line-height: 2.142857142857143;
        @include pc {
            font-size: 1.5rem;
        }
    }
    ul {
        padding-left: 18px;
    }
    strong {
        font-weight: bold;
    }
    time {
        font-family: $baseFont;
        font-weight: 300;
        font-size: 12px;
        color: #383838;
        letter-spacing: 0.025em;
        line-height: 2.3333333333333335;
    }

    a {
        font-family: $baseFont;
        font-weight: $reg;
        font-size: 14px;
        color: #383838;
        letter-spacing: 0.025em;
        line-height: 1.7142857142857142;
        word-break: break-all;
        text-decoration: underline;
        transition: all 0.25s ease-in;

        @include pc {
            font-size: 15px;
            &:hover {
                color: $gold;
            }
        }
    }
    a[target="_blank"] {
        &:after {
            content: "";
            background-image: url("#{$imagePath}/icon/ico_myGutenberg_post_ancherLink.svg");
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            display: inline-block;
            width: 20px;
            height: 13px;
            margin-left: 1.5px;
        }
    }

    img {
        border-radius: 5px !important;
    }

    .is-style-rounded {
        img {
            border-radius: 50% !important;
        }
    }
    figcaption {
        font-size: 1rem;
        font-family: $baseFont;
        color: $darkTxtCol;
    }
}

.myGutenberg_post_info {
    padding-top: 45px;

    @include pc {
        padding-top: 45px;
    }
}
.myGutenberg_post_contents {
    margin-top: 14px;

    @include pc {
        margin-top: 4px;
    }
}

.backToArchiveNtnWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 81.9px;

    @include pc {
        margin-top: 110px;
    }
}

.backToArchiveNtn {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: #266045;
    letter-spacing: 0.04em;
    display: inline-block;
    padding: 10px 40px;
    border-radius: 50px;
    border: 1px solid $baseGreen;
    display: flex;
    justify-content: center;
    align-content: center;
    transition: all 0.25s ease-in;

    @include pc {
        &:hover {
            background-color: $baseGreen;
            color: #fff;
            .backToArchiveNtn_ico {
                width: 14px;
                &:after {
                    width: 100%;
                    background-color: #fff;
                }
            }
            .backToArchiveNtn_ico_edge {
                &:before,
                &:after {
                    background-color: #fff;
                }
            }
        }
    }
}

.backToArchiveNtn_ico {
    transition: all 0.25s ease-in;
    width: 12.5px;
    margin-right: 10px;
    position: relative;
    display: flex;
    align-items: center;
    position: relative;

    &:after {
        border-radius: 10px;
        transition: all 0.25s ease-in;
        display: block;
        content: "";
        width: 50%;
        height: 2px;
        background-color: #266045;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
    }
}

.backToArchiveNtn_ico_edge {
    position: absolute;
    width: 7px;
    height: 7px;
    $deg: -45deg;
    top: 50%;
    left: 0;

    transform: translate(0, -50%) rotate(-45deg);

    &:before,
    &:after {
        border-radius: 50px;
        background-color: #266045;
        position: absolute;
        $deg: 225deg;
        transition: all 0.25s ease-in;
        background-color: #266045;
        content: "";
    }

    &:before {
        width: 100%;
        height: 2px;
    }

    &:after {
        width: 2px;
        height: 100%;
    }
}
