.front_AcoDionSec {
    position: relative;
    padding-top: 47px;
    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(-60%) translateX(-50%);
        background-image: url("#{$imagePath}/global/bg_tree_stump.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 300px;
        height: 300px;
        @include pc {
            width: 450px;
            height: 450px;
            transform: translateY(-35%) translateX(-50%);
        }
    }
}

.front_AcoDionSec_treebk {
    position: absolute;
    top: 100%;
    right: 0;
    width: 200px;
    height: 200px;
    transform: translateY(-50%) translateX(0%);
    background-color: red;
    overflow: hidden;

    display: none;
    @include pc {
        width: 400px;
        height: 400px;
    }
    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 20%;
        background-image: url("#{$imagePath}/global/bg_tree_stump.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 150%;
        height: 150%;
        z-index: -1;
    }
}

.front_AcoDionSec_wrapBtn {
    width: 300px;
    margin: 50px auto;
    margin-bottom: 80px;

    @include pc {
        margin: 70px auto;
        margin-bottom: 110px;
    }
}
