.front_gallery {
    background-color: #eff4f2;
    padding: 80px 0 60px 0;
    position: relative;
    overflow: hidden;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        transform: translateY(-60%) translateX(40%);
        background-image: url("#{$imagePath}/global/bg_tree_stump.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 300px;
        height: 300px;
        @include pc {
            width: 400px;
            height: 400px;
            transform: translateY(145%) translateX(40%);
        }
    }
}

.front_gallery_contents {
    margin-bottom: 40px;

    @include pc {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 70px;
    }
}

.front_gallery_contents_ttl {
    font-weight: bold;
    position: relative;

    @include pc {
        font-size: 4rem;
    }

    &:after {
        content: "";
        width: 40px;
        height: 1px;
        background: $baseGreen;
        position: absolute;
        bottom: 16px;
        transform: translateX(30%);

        @include pc {
            bottom: 20px;
        }
    }
}

.front_gallery_contents_gallery {
    margin-bottom: 50px;
    @include pc {
        display: flex;
        justify-content: space-between;
    }

    // for spacing
    .slick-slide {
        margin: 0 20px;

        @include pc {
            margin: initial;
            width: 29% !important;
            &:last-of-type {
                display: none;
            }
        }
    }

    .slick-list {
        margin: 0 -20px;

        @include pc {
            margin: initial;
        }
    }

    /* the parent */
    .slick-list {
        width: 92.5%;
        margin: 0 auto;
        overflow: visible;

        @include pc {
            max-width: calc(1100px - 20px);
            transform: translate3d(10px, 0px, 0px) !important;
        }
    }

    .slick-track {
        @include pc {
            display: flex !important;
            justify-content: space-between;
            align-items: center;

            &:after,
            &:before {
                display: none;
            }
        }
    }
}

.front_gallery_contents_gallery_item_imageWrap_bk {
    // box-sizing: border-box;
}

.front_gallery_contents_gallery_item {
    border: 1px solid $gold;
    box-sizing: border-box;

    &--last {
        .front_gallery_contents_gallery_item_imageWrap_bk {
            border: 1px solid #fff;
        }
        &:active {
            .front_gallery_contents_gallery_item_imageWrap {
                transform: scale(1.01);
            }
        }
    }
}

.front_gallery_contents_gallery_item_imageWrap {
    transition: all ease 0.1s;
    transform: translate(-12.5px, 12.5px);
    width: 100%;
    @include pc {
        transform: translate(-20px, 20px);
    }
}

.front_gallery_contents_gallery_link {
    background-color: #f0e5cf;
    border: solid 1px #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include pc {
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;
        display: none;
    }

    img {
        width: 23px;
        height: 23px;
        display: block;
        margin: 0 auto;
        margin-top: 10px;
    }
}

.front_gallery_contents_gallery_link_txt {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $darkGreen;
    font-family: $baseFont;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
}

.front_gallery_contents_gallery_link_txt_icn {
    display: block;
    margin: 0 auto;
    width: 24px;
    margin-top: 13px;
}

.front_btn--gall {
    width: 250px;
    margin: 0 auto;

    @include pc {
        width: 190px;
    }
}
