@import "./appe-hero/appe-hero";
@import "./appe-fullBox/appe-fullBox";
@import "./appe-galleryCircles/appe-galleryCircles";
@import "./appe-squareBoxesBig/appe-squareBoxesBig";
@import "./appe-SquareBoxesSmall/appe-SquareBoxesSmall";
@import "./appe-fullBox/appe-fullBox";

//共通セクションデザイン（斜め）
.appe_diagonalSec {
    position: relative;
    padding: calc(100vw * 0.12) 0;
    overflow: hidden;
    margin-top: -3em;
    @include pc {
        margin-top: -10em;
        position: relative;
        z-index: 0;
    }
}

.appe_diagonalSecBk {
    position: absolute;
    transform-origin: center;
    transform: rotate(-6deg);
    width: 200vw;
    height: calc(100% - 0.18 * (0.18 * 50% + 50vw) * 2);
    top: calc(0.18 * (0.17 * 50% + 50vw));
    left: -50vw;
    outline: 10px solid $bkDarkbeige;
    outline-offset: -10px;
    pointer-events: none;
    @include pc {
        background-size: 52%;
    }

    background-image: initial;
    outline: initial;
    outline-offset: initial;
    background: $bkLightbeige;
    transform: rotate(-11deg);
}
