.template_h_contents_section {
    // documentsのセクション
    &:first-of-type {
        padding-bottom: 62px;

        @include pc {
            padding-bottom: 76px;
        }
    }

    // free seminarのセクション（一応firstのその他）
    &:not(:first-of-type) {
        padding-top: 50px;

        @include pc {
            padding-top: 25px;
        }
    }
    &--bk {
        background-color: $bkLightbeige;
        padding-bottom: 65px;
        @include pc {
            padding-bottom: 105px;
        }
        a {
            color: $darkTxtCol;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.template_h_contents_heading {
    font-family: $secFont;
    font-weight: $bol;
    font-size: 26px;
    color: $baseGreen;
    letter-spacing: 0.025em;

    @include pc {
        text-align: center;
        padding-top: 50px;
        position: relative;
        padding-bottom: 14.5px;
        margin-bottom: 55px;
        &::after {
            content: "";
            width: 30px;
            height: 1px;
            background: $gold;
            position: absolute;
            bottom: 0;
            right: 50%;
            transform: translate(50%, 50%);
        }
    }

    &--mb_center {
        text-align: center;
        position: relative;
        padding-bottom: 14px;

        &::after {
            content: "";
            width: 30px;
            height: 1px;
            background: $gold;
            position: absolute;
            bottom: 0;
            right: 50%;
            transform: translate(50%, 50%);
        }

        @include pc {
            margin-bottom: 0;
        }
    }
}

.template_h_contents_sub_heading {
    font-family: $baseFont;
    font-weight: $bol;
    font-size: 18px;
    color: #266045;
    letter-spacing: 0.025em;
    line-height: 1.4444444444444444;
    margin-top: 70px;

    &--seminar_focus {
        margin-top: initial;
        margin-bottom: 15px;
    }

    @include pc {
        margin-top: initial;
        font-size: 20px;
        &--seminar_focus {
            margin-bottom: 31px;
        }

        &--accredited_org {
            margin-top: 114px;
        }
    }
}

.template_h_contents_mainText {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 15px;
    color: $orange;
    letter-spacing: 0.025em;
    line-height: 1.7333333333333334;
    text-align: center;
    padding-top: 20px;

    a {
        color: inherit;
        text-decoration: underline;
    }

    @include pc {
        padding-top: 18.5px;
    }
}

.template_h_contents_wrapImageAndText {
    @include pc {
        margin-top: 66px;
    }
}

.template_h_contents_wrapList {
    @include pc {
        margin-top: 109px;
    }
}

.image_with_text {
    width: 100%;
    max-width: 334px;
    margin: 0 auto;
    padding-bottom: 15px;
    position: relative;
    margin-top: 51px;
    margin-bottom: 24.1px;

    img {
        position: relative;
        width: 318px;
        transform: translate(0, 16px);
        z-index: 2;
    }

    &:after {
        border: 1px solid $gold;
        box-sizing: border-box;
        position: absolute;
        content: "";
        width: 318px;
        height: calc(100% - 16px);
        top: 0;
        right: 0;
        z-index: 1;
    }

    @include pc {
        max-width: 423px;
        margin-top: initial;
        margin-bottom: initial;
        float: right;
        margin-left: 40px;
        padding-bottom: 20px;

        img {
            width: 400px;
            transform: translate(0, 20px);
        }

        &:after {
            width: 400px;
            height: calc(100% - 20px);
        }
    }
}

.template_h_contents_baseText {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    line-height: 2.142857142857143;

    @include pc {
        font-size: 15px;
    }
}

.template_h_contents_baseList {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    line-height: 1.7857142857142858;
    @include pc {
        font-size: 15px;
    }
}

.template_h_contents_baseList_item {
    position: relative;
    padding-left: 14px;
    margin-top: 30px;
    &:before {
        content: "\02022";
        left: 0;
        position: absolute;
    }
    @include pc {
        margin-top: 34px;
    }
    &:first-of-type {
        margin-top: 38px;

        @include pc {
            margin-top: 41px;
        }
    }

    &--initialFirstType {
        &:first-of-type {
            margin-top: 30px;
            @include pc {
                margin-top: initial;
            }
        }
    }
}

// ここからaccredited organizations

.accredited_org_listWrap {
    background-color: #fff;
    padding-bottom: 27px;
    padding-left: 28px;
    padding-right: 28px;
    margin-top: 22px;

    &:not(:first-of-type) {
        margin-top: 15px;
    }

    @include pc {
        display: flex;
        align-items: center;
        padding-top: 35px;
        padding-bottom: 34px;
        padding-left: 40px;
        padding-right: 40px;
        margin-top: 31px;
    }
}

.accredited_org_listTitleWrap {
    display: flex;
    justify-content: center;
    align-content: center;
    padding-bottom: 42.5px;
    padding-top: 43.5px;
    @include pc {
        margin-bottom: 6px;
        margin-bottom: initial;
        margin-right: 50px;
    }
}

.accredited_org_list {
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 14px;
    letter-spacing: 0.025em;
    line-height: 1.7857142857142858;
    @include pc {
        font-size: 15px;
        padding-right: 2.5em;
    }
}

.accredited_org_list_item {
    position: relative;
    padding-left: 14px;
    margin-top: 17px;
    color: $darkTxtCol;
    display: block;
    text-decoration: underline;

    &:before {
        content: "\02022";
        left: 0;
        position: absolute;
    }

    @include pc {
        margin-top: 29px;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }

    &--marginTopIni {
        margin-top: initial !important;
    }
}

//

.accredited_org_listTitle {
    font-family: $baseFont;
    font-weight: $bol;
    font-size: 1.6rem;
    color: $orange;
    letter-spacing: 0.025em;
    text-align: center;
    display: inline-block;
    position: relative;
    text-align: center;

    @include pc {
        min-width: 62px;
    }

    &:after,
    &:before {
        content: "";
        width: 25px;
        height: 1px;
        position: absolute;
        background: $gold;
    }
    &--canada {
        // 上
        &:after {
            bottom: 100%;
            left: 100%;
            transform: translate(-20px, 0) rotate(-45deg);
            transform-origin: left;
        }
        // 下
        &:before {
            top: 100%;
            right: 100%;
            transform: translate(20px, 5px) rotate(-45deg);
            transform-origin: right;
        }
    }
    &--usa {
        &:after {
            bottom: 100%;
            right: 0;
            transform: translate(75%, 0) rotate(-45deg);
            transform-origin: left;
        }
        // 下
        &:before {
            top: 100%;
            left: 0;
            transform: translate(-80%, 2.5px) rotate(-45deg);
            transform-origin: right;
        }
        @include pc {
            // 上
            &:after {
                transform: translate(15%, 0) rotate(-45deg);
            }
            // 下
            &:before {
                transform: translate(-15%, 2.5px) rotate(-45deg);
            }
        }
    }
}

.template_c_contents_link {
    background-color: $bkLightbeige;
    background-color: transparent;
    position: relative;
    margin-top: 40px;
    margin-bottom: 40px;
    @include pc {
        display: none;
    }
}
