@import "./subNav/subNav";

.headernNavWrap {
    width: 100%;
    top: 100%;
    left: 0;
    position: absolute;
    height: 0;
    min-height: 0;
    overflow-y: hidden;
    transition: min-height 0.5s;

    &--border_top {
        border-top: 1px solid #eaeaea;
    }

    @include pc {
        position: initial;
        border-top: none;
        display: block !important;
        height: initial;
        overflow-y: initial;
    }

    &--open {
        background: #fff;
        display: flex !important;
        flex-direction: column;
        // transition: height 0.5s;
        transition: min-height 0.5s;
        overflow-y: scroll;

        // .headernNavWrap_contents {
        //     opacity: 1;
        // }
        // .global_menu {
        //     opacity: 1;
        // }
        @include pc {
            height: initial;
            overflow-y: initial;
            display: initial;
            // モバイルの時にjsで生成される値
            min-height: initial !important;
        }
    }
}

.global_menu {
    opacity: 0;
    // transition: all 1s ease;
    @include pc {
        display: flex;
        justify-content: flex-end;
        width: 100%;
        height: 100%;
        opacity: inherit;
    }

    &--show {
        opacity: 1;
        transition: all 1s ease;
    }
}

.wrapMainandSub {
    width: 100%;

    @include pc {
        width: initial;
        &:first-of-type {
            border-bottom: initial;
        }

        &:last-of-type {
            margin-left: 2px;
        }
    }

    &--mobileOnly {
        @include pc {
            display: none;
        }
    }

    &--pcOnly {
        display: none;
        @include pc {
            display: block;
        }
    }
}

.global_menu_li {
    border-top: 1px solid #eaeaea;
    background: #fff;
    &--opa {
        a {
            opacity: 0.5;
            pointer-events: none;
            cursor: default;
            text-decoration: none;
        }
    }
    @include pc {
        border-top: none;
        height: 100%;
        &--opa {
            opacity: 0.5;
            pointer-events: none;
            cursor: default;
            text-decoration: none;
        }
    }

    &--sub {
        @include pc {
            width: 47.5%;
        }
    }

    &--noLine {
        border-top: none;
    }
}

.global_menu_li_aLink {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-family: $baseFont;
    font-size: 1.3rem;
    color: $baseGreen;
    min-height: 60px;
    background: #fff;
    line-height: 1.3846153846153846;
    font-weight: 600;
    cursor: pointer;

    // -webkit-text-stroke: 0.4px $baseGreen;
    // text-stroke: 0.4px $baseGreen;

    &--sub {
        min-height: 60px;
        font-size: 1.2rem;
        padding: 0.5em 0;
        @include pc {
            min-height: initial;
            margin-bottom: 1em;
        }
    }

    @include pc {
        padding: 0;
        display: flex;
        height: 100%;
        justify-content: center;
        align-items: center;
        position: relative;
        white-space: nowrap;
        width: 100%;
        transition: all 0.3s ease-in-out;

        &:before,
        &:after {
            transition: all 0.25s ease-in-out;
        }

        &:before {
            position: absolute;
            bottom: 0;
            right: 50%;
            content: "";
            width: 0;
            height: 2.5px;
            background: $gold;
        }
        &:after {
            position: absolute;
            bottom: 0;
            left: 50%;
            content: "";
            width: 0;
            height: 2.5px;
            background: $gold;
        }
        &:hover {
            color: $gold;
            // -webkit-text-stroke: 0.4px $gold;
            // text-stroke: 0.4px $gold;
            &:before {
                width: 50%;
            }
            &:after {
                width: 50%;
            }
        }

        &--active {
            color: $gold;
            // -webkit-text-stroke: 0.4px $gold;
            // text-stroke: 0.4px $gold;
            &:before {
                width: 50%;
            }
            &:after {
                width: 50%;
            }
        }
    }

    // redとgreenだけ
    &--red {
        background-color: #c8786a;
        color: #fff;
        // -webkit-text-stroke: initial !important;
        // text-stroke: initial !important;
    }

    &--green {
        background-color: #266045;
        color: #fff;
        // -webkit-text-stroke: initial !important;
        // text-stroke: initial !important;
    }

    &--red,
    &--green {
        z-index: 1;
        transition: all 0.1s ease-in-out;

        &:before,
        &:after {
            position: absolute;
            bottom: initial;
            right: initial;
            left: initial;
            bottom: initial;
            background-color: initial;
        }

        &:after {
            width: 100%;
            height: 0;
            background-color: #fff;
            top: 0;
            z-index: -1;
        }

        @include pc {
            &:hover {
                color: initial;

                &:after,
                &:before {
                    width: 100%;
                }
            }
        }
    }

    &--hoverActive {
        @include pc {
            &:after,
            &:before {
                width: 50%;
            }
        }
    }

    &--red {
        @include pc {
            &:hover {
                color: #c8786a;
                &:after {
                    height: 98%;
                }
                .global_menu_li_link_txt_smallText {
                    color: #c8786a;
                }
            }
        }
    }
    &--green {
        @include pc {
            &:hover {
                color: #266045;
                &:after {
                    height: 98%;
                }
                .global_menu_li_link_txt_smallText {
                    color: #266045;
                }
            }
        }
    }
}

.navarrw {
    margin-left: auto;
    padding-right: 1.5em;
    display: flex;
    align-items: center;
    justify-content: center;

    @include pc {
        margin-left: initial;
        padding-left: initial;
        margin-right: initial;
        padding-right: inherit;
        display: none;
    }

    &--circle {
        display: none;

        @include pc {
            display: flex;
        }
    }

    &--f_related {
        display: flex;
        justify-content: center;
        align-content: center;
        width: 6px;
        margin-right: 5px;
    }
}

.navarrw_arr {
    margin: auto 0;
    &--plus {
        display: none;
    }
    &--minus {
        display: none;
    }
    &--active {
        display: block;
    }
}

.navarrw_wrapArrow_innerIcon {
    width: 10px;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url(../images//H-page/ico/ico_arrow.svg);
    transition: all 0.15s ease;
}

.global_menu_li_link_txt {
    width: 100%;
    margin-left: 20px;
    letter-spacing: 0.33px;
    @include pc {
        margin-left: initial;
        text-align: center;
        display: block;

        &--appearance_and_patternProduct {
            padding: 0 23px;
        }
        &--structuralWood_and_MassTimberProduct {
            padding: 0 19px;
        }
        &--techSpecs_and_CodeCompliance {
            padding: 0 23px;
        }
        &--whatsNew {
            padding: 0 18px;
        }
        &--aboutUs {
            padding: 0 17px;
        }
        &--FAQs {
            padding: 0 29px;
        }
        &--wildFire_and_fireSmart {
            padding: 0 11px;
        }
        &--whereToBuy {
            padding: 0 11px;
        }
    }

    &--sub {
        @include pc {
            margin-right: 1.5em;
            text-align: initial;
        }
    }
}

.global_menu_li_link_txt_smallText {
    display: block;
    font-family: $baseFont;
    font-size: 10px;
    color: #ffffff;
    letter-spacing: 0.025em;
    line-height: 1.3;
    transition: all 0.1s ease-in-out;
    margin-top: 3.5px;
    &--italic {
        margin-top: initial;
        font-family: $secFont;
        font-size: 11px;
        font-weight: $reg;
        font-style: Italic;
    }
}

.headernNavWrap_contents {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background: $darkGreen;
    padding: 2em 0;
    opacity: 0;

    @include pc {
        display: none;
    }

    &--show {
        opacity: 1;
        transition: all 1s ease;
    }
}

.headernNavWrap_contents_heading {
    font-family: $secFont;
    font-weight: $reg;
    font-size: 1.8rem;
    color: #ffffff;
    letter-spacing: 0em;
    line-height: 1.7777777777777777;
}

.headernNavWrap_contents_flex {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 1em;
    max-width: 300px;
}

.global_menu_li_aLink_small {
    font-family: $secFont;
    font-style: italic;
    font-size: 1.2rem;
    color: #c8a96a;
}

.wrapLabelAndIcon {
    @include pc {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.wrapLabelAndIcon_iconWrap {
    @include pc {
        width: 10px;
        margin-bottom: 2px;
    }
}

.singleSubMenu {
    background: #faf7f1;
    padding-bottom: 100px;
    height: 100%;

    @include pc {
        padding-bottom: initial;
        height: initial;
    }
}
