.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    font-family: $baseFont;
    padding: 0.8em 1.5em;
    line-height: 1.1;
    transition: all 0.25s ease-in;
    border: 1px solid transparent;

    &--raduisMax {
        @include pc {
            border-radius: 50px;
        }
    }

    &--dark {
        background: $baseGreen;
        color: #fff;
    }

    &--light {
        background: #fff;
        color: $baseGreen;
        border: solid 1px $baseGreen;
    }

    &--lightDark {
        background: #fff;
        color: #383838;
    }

    &--transparent {
        color: #fff;
        border: solid 1px #fff;
    }

    &--big {
        font-size: 1.4rem;
    }

    &--small {
        font-size: 0.9rem;
    }

    &--opa {
        opacity: 0.5;
        pointer-events: none;
        cursor: default;
        text-decoration: none;
    }

    &--darkLine {
        border: solid 1px $baseGreen;
        color: $baseGreen;
    }

    &--boxshadow {
        position: relative;
        &:before,
        &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 20px;
        }
        &:before {
            box-shadow: 1px 2px 5px rgb(0 0 0 / 70%);
        }
        &:after {
            box-shadow: -1px -2px 5px rgb(255 255 255 / 40%);
        }
    }
}

.btn_txt {
    &--txtCenter {
        text-align: center;
    }
}

// where to buy ボタンのみ
.btn_inner_small {
    font-size: 0.9rem;
    @include pc {
        padding: 0 1em;
        font-size: 1.4rem;
    }
}
.btn_inner_big {
    font-size: 1rem;
    font-style: $styleIta;
    font-family: $secFont;
    margin-top: 0.25em;
    @include pc {
        padding: 0 1em;
        font-size: 1.1rem;
    }
}

.btn_wrapIcon {
    display: flex;
    justify-content: center;
    align-items: center;

    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    margin-left: 13px;
    transition: all 0.25s ease-in;
    &--mail {
        width: 20px;
        height: 14px;
        background-image: url("#{$imagePath}/icon/ico_mail_green.svg");
    }

    &--phone {
        width: 18px;
        height: 18px;
        background-image: url("#{$imagePath}/icon/ico_tel_green.svg");
    }
}

.btn {
    &:active {
        @include pc {
            &:after {
                box-shadow: initial;
            }
            &:before {
                box-shadow: initial;
            }
        }
    }
    &--mail {
        @include pc {
            &:hover {
                background: $darkGreen;
                color: #fff;
                border-color: #fff;

                .btn_wrapIcon {
                    transform: rotateY(360deg);
                    background-image: url("#{$imagePath}/icon/ico_mail_white.svg");
                }
            }
        }
    }

    &--phone {
        @include pc {
            &:hover {
                background: $darkGreen;
                color: #fff;
                border-color: #fff;

                .btn_wrapIcon {
                    transform: rotateY(360deg);
                    background-image: url("#{$imagePath}/icon/ico_tel_white.svg");
                }
            }
        }
    }
}
