@import "./slickWrap/slickWrap";
@import "./slickItem/slickItem";

.appe-squareBoxesBig {
    padding-top: 3em;
    padding-bottom: 2em;
    background-color: #eff4f2;
    position: relative;
    @include pc {
        padding-bottom: 6em;
    }
    &:before {
        position: absolute;
        top: 0%;
        transform: translate(50%, -50%);
        right: 50%;
        content: "";
        width: 0;
        height: 2.5px;
        background: $gold;
        height: 60px;
        width: 1px;
        z-index: 1;

        @include pc {
            top: 0%;
            transform: translate(50%, -50%);
        }
    }
}
.container_onlyBoxesBig {
    @include pc {
        @include siteBaseWidth();
    }
}
