.accordionList {
    font-family: $secFont;
}
.accordionList_list {
    border-bottom: 1px solid #cfcfcf;
}
.accordionList_list_title {
    font-size: 2rem;
    font-weight: $mid;
    padding: 1.5em 0;

    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $baseGreen;
    line-height: 1.5;
    @include pc {
        font-size: 2.4rem;
        padding: 1.5em 0;
    }

    &--animated {
        color: $gold;
    }
}

.accordionList_list_title_inner {
    width: 90%;
}

.accordionList_list_txt {
    line-height: 1.5;
    padding-bottom: 3em;
    font-family: $baseFont;
    font-size: 1.4rem;
    display: none;
    color: $darkTxtCol;
    letter-spacing: 0.025em;
    line-height: 1.4285714285714286;

    @include pc {
        padding-bottom: 3em;
        font-size: 1.5rem;
    }
}

.accordionList_list_title_arrow {
    width: 20px;
    align-items: center;
    display: flex;
    img {
        width: 100%;
    }
    transition: all 0.2s ease-out;

    &--animated {
        transform: rotate(-180deg);
    }
}
