.pagination_list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 48.5px;

    .first {
        display: block;
        width: 35px;
        font-size: 16px;
        height: 35px;
        margin-right: 1em;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        transition: all 0.2s;
        font-family: $baseFont;
        color: $baseGreen;
        font-weight: $reg;
        border: solid #fff 1px;
        box-sizing: border-box;
        &:hover {
            border: solid $baseGreen 1px;
            color: $baseGreen;
        }
    }
}

.pagination_list_prev,
.pagination_list_pager,
.pagination_list_next,
.pagination_list_last {
    display: block;
    width: 35px;
    font-size: 16px;
    height: 35px;
    margin-right: 1em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    font-family: $baseFont;
    color: $baseGreen;
    font-weight: $reg;
    border: solid #fff 1px;
    box-sizing: border-box;
    &:hover {
        border: solid $baseGreen 1px;
        color: $baseGreen;
    }
}

.pagination_list_current {
    background: #d8a469;
    color: white;
    border: initial !important;

    &:hover {
        color: white;
    }
}

.pagination_list_next {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before {
        content: "";
        width: 5px;
        height: 5px;
        border-top: 1.5px solid $baseGreen;
        border-right: 1.5px solid $baseGreen;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        right: 1px;
    }
}

.pagination_list_prev {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before {
        content: "";
        width: 5px;
        height: 5px;
        border-top: 1.5px solid $baseGreen;
        border-right: 1.5px solid $baseGreen;
        -webkit-transform: rotate(-135deg);
        transform: rotate(-135deg);
        position: relative;
        right: -1px;
    }
}
