.slickItem {
    position: relative;
    margin: 0.5em 0;
}

.squareBoxesBig_imageWrap {
    position: relative;
}

.squareBoxesBig_ttl_withImg {
    position: absolute;
    right: 5%;
    transform: translate(0%, -50%);
    z-index: 1;
    font-family: $baseFont;
    font-weight: $reg;
    font-size: 1.6rem;
    color: #fff;
    letter-spacing: 0.025em;
    line-height: 5;
    text-shadow: 0px 0px 20px #000000;
}

.slickItem {
    &:last-of-type {
        .squareBoxesBig_ttl_withImg {
            top: 90%;
        }
    }
    &:first-of-type {
        .squareBoxesBig_ttl_withImg {
            top: 10%;
        }
    }
    @include pc {
        &:last-of-type {
            .squareBoxesBig_ttl_withImg {
                top: 10%;
            }
        }
        &:first-of-type {
            .squareBoxesBig_ttl_withImg {
                top: 10%;
            }
        }
    }
}
