.footer-content_nav {
    display: none;
    font-family: $baseFont;

    @include pc {
        display: flex;
        justify-content: space-between;
        padding: 100px 0 20px 0;
        border-bottom: solid 1px #dddddd;
    }
}

// content width
.footer-content_nav_row:nth-child(1) {
    @include pc {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 35%;
    }
}

.footer-content_nav_row:nth-child(2) {
    @include pc {
        width: 30%;
    }
}

.footer-content_nav_row:nth-child(3) {
    @include pc {
        width: 25%;
    }
}

// content
.footer-content_nav_item {
    @include pc {
        margin-bottom: 50px;
    }

    // title
    .footer-content_nav_item_title,
    .footer_link {
        @include pc {
            font-weight: bold;
            color: $darkGreen;
            margin-bottom: 20px;
            letter-spacing: 0.02em;
            font-size: 16px;
            vertical-align: middle;
        }
    }

    .footer-content_nav_item_title_row {
        @include pc {
            display: flex;
        }
    }

    .footer_location_icon {
        background-image: url("#{$imagePath}/icon/ico_location_green.svg");
        background-size: auto;
        background-repeat: no-repeat;
        padding: 0 10px;
        vertical-align: middle;
    }

    .color_pink {
        @include pc {
            color: #c8786a;
            line-height: 1.3em;
        }

        .footer_link_light {
            @include pc {
                font-weight: 500;
                font-size: 13px;
            }
        }
    }

    // sub title
    ul {
        border-left: solid 2px #e5d0a6;
        margin-top: 30px;
    }

    li {
        @include pc {
            margin-bottom: 20px;
            padding-left: 30px;
            line-height: 1.5em;
            letter-spacing: 0.02em;
            font-weight: lighter;
            font-size: 13px;
            a {
                color: $darkTxtCol;
                font-weight: 400;
            }
        }

        a:hover {
            @include pc {
                color: $gold;
                border-bottom: solid 1px $gold;
                transition: all 0.25s ease;
            }
        }
    }

    //hover
    .footer-content_nav_item_title:hover {
        .footer_link {
            @include pc {
                color: $gold;
                border-bottom: solid 1px $gold;
                transition: all 0.25s ease;
            }
        }

        .footer_location_icon {
            background-image: url("#{$imagePath}/icon/ico_location_gold.svg");
            transition: all 0.25s ease;
        }
    }

    .color_pink:hover {
        @include pc {
            color: $gold;
            border-bottom: solid 1px $gold;
            transition: all 0.25s ease;
        }
    }
}
